.awards {
  background-color: #122947;
  color: #fff;
  text-align: center;

}

.awards .icon {
  width: 100px;
  height: 100px;
  line-height: 100px;
  margin: auto;
  margin-top: 50px;
  background: rgba(247, 244, 244, 0.1);
